body {
  margin: 0; /* ‡∏•‡∏ö margin ‡πÄ‡∏£‡∏¥‡πà‡∏°‡∏ï‡πâ‡∏ô‡∏Ç‡∏≠‡∏á body */
  padding: 0; /* ‡∏•‡∏ö padding ‡πÄ‡∏£‡∏¥‡πà‡∏°‡∏ï‡πâ‡∏ô‡∏Ç‡∏≠‡∏á body */
  width: 100%; /* ‡∏Ñ‡∏£‡∏≠‡∏ö‡∏Ñ‡∏•‡∏∏‡∏°‡∏ó‡∏±‡πâ‡∏á‡∏´‡∏ô‡πâ‡∏≤‡∏à‡∏≠ */
  height: 100%; /* ‡∏Ñ‡∏£‡∏≠‡∏ö‡∏Ñ‡∏•‡∏∏‡∏°‡∏ó‡∏±‡πâ‡∏á‡∏´‡∏ô‡πâ‡∏≤‡∏à‡∏≠ */
  background: linear-gradient(to bottom, #fffdfa, #fff7ea);
}

.booking-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 80px;
}

.top-section {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  background-color: #ffdee3;
  padding: 40px;
  position: relative;
}
.top-section .text-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}
.top-section .image-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; /* ‡πÄ‡∏û‡∏¥‡πà‡∏°‡πÄ‡∏û‡∏∑‡πà‡∏≠‡πÅ‡∏™‡∏î‡∏á‡∏ß‡πà‡∏≤‡∏Ñ‡∏•‡∏¥‡∏Å‡πÑ‡∏î‡πâ */
}

/* ‡∏õ‡∏∏‡πà‡∏° */
.btn {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.btn:hover {
  background-color: #0056b3;
}

/* Modal ‡∏™‡∏≥‡∏´‡∏£‡∏±‡∏ö‡∏´‡∏ô‡∏±‡∏á‡∏™‡∏∑‡∏≠ */
.book-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  position: relative;
  padding: 20px;
  border-radius: 10px;
  width: 1000px;
  height: 600px;
}

.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: red;
  color: white;
  border: none;
  border-radius: 50%;
  width: 30px;
  height: 30px;
  cursor: pointer;
}

/* ‡∏™‡πà‡∏ß‡∏ô‡∏•‡πà‡∏≤‡∏á */
.bottom-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
  background-color: #ffd0e7;
  padding: 40px;
}

.bottom-section .text-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

.bottom-section .image-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}
.image {
  max-width: 400px;
  height: auto;
  transition: transform 0.3s ease;
}
.image:hover {
  transform: scale(1.05);
}
.image-book {
  max-width: 400px;
  height: auto;
  transition: transform 0.3s ease;
  border: #000 solid 4px;
  border-radius: 10px;
  padding: 60px 0px 60px 0px;
}
.image-book:hover {
  transform: scale(1.05);
}
.bottom-section {
  height: 500px;
}
